#bird {
  width: 2em;
  height: 2em;
  position: absolute;
  top: 250px;
  left: 40px;
  margin-bottom: 20px;
}

body {
  background-color: #ffffff;
  font-family: 'Noto Sans', sans-serif;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}
#score {
  font-size: 3.5em;
  color: #f44336;
  position: relative;
  font-weight: bold;
  text-align: center;
  z-index: 15;
}
#gameOver {
  font-size: 3em;
  color: #03A9F4;
  position: relative;
  font-weight: bold;
  top: 150px;
  text-align: center;
  z-index: 15;
  width: 30%;
  height: 170px;
  margin: 0 auto;
  background-color: #424242;
}

#gameOver div {
  padding-top: 2%;
}

#gameOver button {
  background-color: #009688;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  margin-bottom: 2%;
}
#startScreen {
  color: #03A9F4;
  font-size: 2em;
  font-weight: bold;
  width: 100%;
  height: 100%;
  display: table;
  position: absolute;
  text-align: center;
}

#startScreen div {
  display: table-cell;
  vertical-align: middle;
}

.startScreen_mb {
  margin-bottom: 15px;
}

.startScreen_imageWrapper {
  text-align: center;
  margin-bottom: 20px;
}

.startScreen_image {
  width: 150px;
}

.startScreen_paragraph {
  text-align: center;
  font-size: 13px;
}
.container {
    display: flex;
}

.container_row {
    flex-direction: row;
}

.container_column {
    flex-direction: column;
}

.container_justify-start {
    justify-content: flex-start;
}

.container_justify-center {
    justify-content: center;
}

.container_justify-end {
    justify-content: flex-end;
}

.container_justify-between {
    justify-content: space-between;
}

.container_align-start {
    align-items: flex-start;
}

.container_align-center {
    align-items: center;    
}

.container_align-end {
    align-items: flex-end;
}

/*
    Information containet
*/

.info-container {
    position: relative;
    padding: 30px 20px;
    background-color: #fff;
    width: 70%;
    margin: 0 auto;
}

.info-container::before {
    content: '';
    position: absolute;
    width: 95%;
    height: 10px;
    display: block;
    top: -10px;
    left: 50%;
    background: #fff;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
}

.info-container::after {
    content: '';
    position: absolute;
    width: 95%;
    height: 10px;
    display: block;
    bottom: -10px;
    left: 50%;
    background: #fff;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
}

.button {
    color: #ffffff;
    text-decoration: none;
    background-size: contain;
    width: 200px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.prize_common-image {
