.container {
    display: flex;
}

.container_row {
    flex-direction: row;
}

.container_column {
    flex-direction: column;
}

.container_justify-start {
    justify-content: flex-start;
}

.container_justify-center {
    justify-content: center;
}

.container_justify-end {
    justify-content: flex-end;
}

.container_justify-between {
    justify-content: space-between;
}

.container_align-start {
    align-items: flex-start;
}

.container_align-center {
    align-items: center;    
}

.container_align-end {
    align-items: flex-end;
}

/*
    Information containet
*/

.info-container {
    position: relative;
    padding: 30px 20px;
    background-color: #fff;
    width: 70%;
    margin: 0 auto;
}

.info-container::before {
    content: '';
    position: absolute;
    width: 95%;
    height: 10px;
    display: block;
    top: -10px;
    left: 50%;
    background: #fff;
    transform: translate(-50%);
}

.info-container::after {
    content: '';
    position: absolute;
    width: 95%;
    height: 10px;
    display: block;
    bottom: -10px;
    left: 50%;
    background: #fff;
    transform: translate(-50%);
}
